import React from 'react';
import ReactDOM from 'react-dom';
import Router from './router.js';

function render() {
  Router.dispatch(window.location.hash.substring(1), renderRoot);
}

function renderRoot(state, component) {
  let screen  = React.createElement(component, {id: state.params.id, docketID: state.params.docketID, ballotID: state.params.ballotID});
  let root    = document.getElementById('app');
  ReactDOM.render(screen, root);
}

window.addEventListener('hashchange', () => render());
render()
