import React, {Component} from 'react';
import Style from '../../Style';

export default class NewMemberForm extends Component {
  constructor() {
		super(...arguments);
		this.state = {name: ''};
	}
  
  render() {
    return React.DOM.form(
			{onSubmit: this.pushCandidate.bind(this)},
			this.newMemberField(this.state.name)
		);
  }
  
  newMemberField(name) {
    return React.DOM.input({
			style: Style.input,
			onChange: this.stageCandidate.bind(this),
			value: name
		});
  }

	stageCandidate(event){
		event.preventDefault();
		event.stopPropagation();

		this.setState({name: event.target.value});
	}

	pushCandidate(event) {
		event.preventDefault();
		event.stopPropagation();

		this.props.membersBase.push(this.state);
		this.setState({name: ''});
	}
}
