import { Router } from 'react-routing';
import Marketing from './screens/marketing/Screen';
import Docket from './screens/docket/Screen';
import Ballot from './screens/ballot/Screen';
import BallotService from './service/ballot';
import 'babel-polyfill';

const visitBallot = ballot => {
  const docketID = ballot.parent().parent().key();
  const ballotID = ballot.key();
  window.location.href = `#/dockets/${docketID}/ballots/${ballotID}`;
}
  
const createAndVisitBallot = s =>
  BallotService.createBallot(s.params.id).then(visitBallot);

export default new Router(on => {
  on('/dockets/:id/ballots/new', createAndVisitBallot);
  on('/dockets/:docketID/ballots/:ballotID', s => Ballot);
  on('/dockets/:id/', s => Docket);
  on('/', s => Marketing);
});