import React 				from 'react';
import Title        from './Title';
import AbstractScreen        from '../AbstractScreen';
import Finalize     from './Finalize';
import CandidateSet    from './CandidateSet';
import DataService from '../../DataService';
import NewMemberForm 	  from './NewMemberForm';

export default class Candidate extends AbstractScreen {
	constructor() {
		super(...arguments);
		this.state = {candidates:[]};
	}

	render() {
		let style = {
			background: 'rgb(60, 150, 130)',
			height: '100%',
			fontFamily: 'sans-serif',
			padding: 20, textAlign: 'center'
		};
		let docketBase = DataService.child(`dockets/${this.props.id}`);

		let title = React.createElement(Title,
			{text: this.state.title, titleRxzef: docketBase.child('title')}
		);

		let finalize = React.createElement(Finalize,
			{docketBase: docketBase, final: this.state.final, docketID:this.props.id}
		);

    let candidateSet = React.createElement(CandidateSet, {
				members: this.state.candidates
		});
    
    let newMemberForm = React.createElement(
      NewMemberForm, {membersBase: docketBase.child('candidates')}
    )

    return React.DOM.div(
			{style},
			title,
			candidateSet,
      newMemberForm,
			finalize
		);
  }
}
